.input-wrapper {
  &--lt {
    .input-wrapper{
      width: 100%;
      max-width: 130px;
      &:not(:last-child) {
        margin-right: 10px;
      }
      @media(max-width: 480px) {
        max-width: 45%;
      }
    }
  }
}
.input-wrapper {
  margin-bottom: 30px;
  max-width: 500px;
  @media (max-width: 767px) {
    margin-bottom: 20px;
  }
  span {
    display: block;
    font-size: 14px;
    margin-bottom: 10px;
    font-weight: 600;
  }
}
.input,
.select-input{
  display: block;
  width: 100%;
  height: 40px;
  border-radius: 10px;
  border: 1px solid $grey-bg;
  font-size: 16px;
  font-family: 'Poppins', sans-serif;
  color: #000;
  padding-left: 15px;
  transition: all .3s;
  &:focus {
    box-shadow: 0 3px 10px rgba(#000, .1);
  }
}
.select-input {
  background: #fff url('../../img/icons/arrow_down.svg') calc(100% - 15px) center no-repeat;
  padding-right: 30px;
  &:focus {
    box-shadow: none;
  }
}
